import React, { Component } from 'react';
import 'react-calendar/dist/Calendar.css';
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import DatePicker from "react-datepicker";
import config from '../../constant/config.js';
import Select from 'react-select'
const axios = require('axios');

// import { Multiselect } from "multiselect-react-dropdown";

// const options = [
//     { value: 'chocolate', label: 'Chocolate' },
//     { value: 'strawberry', label: 'Strawberry' },
//     { value: 'vanilla', label: 'Vanilla' }
// ]

class Innerfilter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // startDate: new Date(),
            // endDate: new Date(),

            selectedAccountOption: null,
            selectedSymbolOption: null,
            selectedSymbolOptionTo: null,
            selectedAccountOptionTo: null,
            startDate: null,
            endDate: null,
            startDateTo: null,
            endDateTo: null,
            allAccounts: [],
            allSymbols: [],
            profileName: '',
            error: ''
            // options: [{ name: 'Srigar', id: 1 }, { name: 'Sam', id: 2 }],

            // plainArray: ["Option 1", "Option 2", "Option 3", "Option 4", "Option 5"],
            // objectArray: [
            //     { key: "Option 1", cat: "Group 1" },
            //     { key: "Option 2", cat: "Group 1" },
            //     { key: "Option 3", cat: "Group 1" },
            //     { key: "Option 4", cat: "Group 2" },
            //     { key: "Option 5", cat: "Group 2" },
            //     { key: "Option 6", cat: "Group 2" },
            //     { key: "Option 7", cat: "Group 2" }
            // ],
            // selectedValues: [
            //     { key: "Option 1", cat: "Group 1" },
            //     { key: "Option 2", cat: "Group 1" }
            // ]
        };


    }
    componentDidMount() {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchAllAccounts')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ allAccounts: response.data.rows })
                }
            })
        axios.get(config.baseurl + '/accounts/fetchAllSymbol')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ allSymbols: response.data.rows })
                }
            })
    }
    // addItem = () => {
    //     this.selectedValues.push({ key: "Option 3", cat: "Group 1" });
    // }
    handleDropdownChange = (e) => {
        this.setState({ selectValue: e.target.value });
    }
    handleChangeDate = date => {

        this.setState({
            startDate: date
        });
    }
    handleEndDate = date => {

        this.setState({
            endDate: date
        });
    }
    handleChangeDateTo = date => {
        this.setState({
            startDateTo: date,
            error: ''
        });
    }
    handleEndDateTo = date => {
        this.setState({
            endDateTo: date
        });
    }
    handleChange = (event) => {
        //this.setState({value: event.option});
        this.setState({ value: Array.from(event.target.selectedOptions, (item) => item.value) });
    }

    renderAccountSelect = () => {
        const { selectedAccountOption, allAccounts } = this.state;

        let options = allAccounts.map(data => {
            return { value: data.id, label: data.login };
        })
        return (
            <Select
                placeholder="Select Account..."
                value={selectedAccountOption}
                onChange={this.handleChangeAccount}
                options={options}
            />
        )
    }

    handleChangeAccountTo = selectedAccountOptionTo => {
        this.setState({ selectedAccountOptionTo,error:'' });
    };
    renderAccountSelectTo = () => {
        const { selectedAccountOptionTo, allAccounts } = this.state;

        let options = allAccounts.map(data => {
            return { value: data.id, label: data.login };
        })
        return (
            <Select
                placeholder="Select Account..."
                value={selectedAccountOptionTo}
                onChange={this.handleChangeAccountTo}
                options={options}
            />
        )
    }


    handleChangeAccount = selectedAccountOption => {
        this.setState({ selectedAccountOption });
    };
    handleChangeSymbol = selectedSymbolOption => {
        this.setState({ selectedSymbolOption });
    };
    renderSymbolSelect = () => {
        const { selectedSymbolOption, allSymbols } = this.state;
        let options = allSymbols.map(data => {
            return { value: data.name, label: data.name };
        })
        return (
            <Select
                placeholder="Select Symbol..."
                value={selectedSymbolOption}
                isMulti
                onChange={this.handleChangeSymbol}
                options={options}
            />
        )
    }
    handleChangeSymbolTo = selectedSymbolOptionTo => {
        this.setState({ selectedSymbolOptionTo });
    };
    renderSymbolSelectTo = () => {
        const { selectedSymbolOptionTo, allSymbols } = this.state;
        let options = allSymbols.map(data => {
            return { value: data.name, label: data.name };
        })
        return (
            <Select
                placeholder="Select Symbol..."
                value={selectedSymbolOptionTo}
                isMulti
                onChange={this.handleChangeSymbolTo}
                options={options}
            />
        )
    }

    handleprofileName(e) {
        this.setState({ profileName: e.target.value })

    }
    formDataSubmit(e) {
        e.preventDefault();
        let self = this
        if (this.state.selectedAccountOption == null || this.state.selectedAccountOption == '') {
            this.setState({ error: 'accountoption' })
            return
        } else if (this.state.startDate == null || this.state.startDate == '') {
            this.setState({ error: 'startdate' })
            return
        } else if (this.state.selectedSymbolOption == null || this.state.selectedSymbolOption == '') {
            this.setState({ error: 'symbolselect' })
            return
         } 
          else if (this.state.selectedAccountOptionTo == null || this.state.selectedAccountOptionTo == '') {
            this.setState({ error: 'accountoptionto' })
            return
        }else if (this.state.startDateTo == null || this.state.startDateTo == '') {
           this.setState({ error: 'startDateTo' })
           return
        }
        else if (this.state.selectedSymbolOptionTo == null || this.state.selectedSymbolOptionTo == '') {
            this.setState({ error: 'selectedSymbolOptionTo' })
            return
         }
         else{
            this.setState({error:''})
        }
        let fromSymbol = this.state.selectedSymbolOption;
        let symbolFrom = fromSymbol.map((data) => {
            return data.value
        })
        let toSymbol = this.state.selectedSymbolOptionTo;
        let symbolto = toSymbol.map((data) => {
            return data.value
        })

        let Data = {
            profile_name: this.state.profileName,
            from_account_id: this.state.selectedAccountOption.value,
            to_account_id: this.state.selectedAccountOptionTo.value,
            startdateFrom: this.state.startDate,
            enddateFrom: this.state.endDate,
            startdateTo: this.state.startDateTo,
            enddateTo: this.state.endDateTo,
            from_symbols: JSON.stringify(symbolFrom),
            to_symbols: JSON.stringify(symbolto),
            createdAt: new Date(),
            updatedAt: new Date(),

        }
        // console.log(Data,'data');

        axios.post(config.baseurl + '/filter/addfilterdata', Data)
            .then(function (response) {
                if (response.status == 200) {
                    self.props.onHide()

                }
            })
            .catch(function (error) {
                console.log(error);
            });

    }
    crossClick(e) {
        e.preventDefault();
        this.props.onHide()
    }

    render() {
        // const {  objectArray } = this.state;

        return (
            <div className="content">
                <Grid fluid>
                    <div className="symbol-box">
                        <div className="cross_div"><a onClick={(e) => this.crossClick(e)}><i className="fa fa-times" aria-hidden="true"></i></a></div>
                        <div className="terminal-text" >
                            <label className=""> Profile Name </label>
                            <input type="text" name="profilename" className="Itext" placeholder="Type Profile Name" onChange={(e) => this.handleprofileName(e)} />
                        </div>
                        <form>
                            <Row>
                                <Col md={6}>
                                    <div className="card">
                                        <div style={{ padding: '10px 20px 0px' }}>
                                            <span style={{ fontWeight: 'bold' }}>Account A</span>
                                        </div>
                                        <table className="table mt20">
                                            <tbody>
                                                <tr>
                                                    <td className="b-clr">
                                                        <span>Account :</span>
                                                    </td>

                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {/* <Multiselect
                                                        options={objectArray}
                                                        singleSelect
                                                        displayValue="key"
                                                    /> */}
                                                        {this.renderAccountSelect()}
                                                        {(this.state.error == 'accountoption') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Your Account</p>}

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>From:  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.startDate}
                                                            onChange={this.handleChangeDate}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDate}
                                                            endDate={this.state.endDate}
                                                            strictParsing
                                                        />
                                                        {(this.state.error == 'startdate') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Start Date</p>}
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>To :  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.endDate}
                                                            onChange={this.handleEndDate}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDate}
                                                            endDate={this.state.endDate}
                                                        />
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>Symbol :  </span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {/* <Multiselect
                                                        options={objectArray}
                                                        displayValue="key"
                                                        showCheckbox={true}
                                                    /> */}
                                                        {this.renderSymbolSelect()}
                                                        {(this.state.error == 'symbolselect') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Your Symbol </p>}
                                                    </td>
                                                </tr>

                                            </tbody>
                                        </table>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="card">
                                        <div style={{ padding: '10px 20px 0px' }}>
                                            <span style={{ fontWeight: 'bold' }}>Account B</span>
                                        </div>

                                        <table className="table mt20">
                                            <tbody>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>Account :</span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {this.renderAccountSelectTo()}
                                                        {(this.state.error == 'accountoptionto') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Your Account</p>}
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>From:  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.startDateTo}
                                                            onChange={this.handleChangeDateTo}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDateTo}
                                                            endDate={this.state.endDateTo}
                                                            strictParsing
                                                        />
                                                        {(this.state.error == 'startDateTo') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Start Date</p>}
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span>To :  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.endDateTo}
                                                            onChange={this.handleEndDateTo}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDateTo}
                                                            endDate={this.state.endDateTo}
                                                        />
                                                        
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr">
                                                        <span>Symbol :  </span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {/* <Multiselect
                                                        options={objectArray}
                                                        displayValue="key"
                                                        showCheckbox={true}
                                                    /> */}
                                                        {this.renderSymbolSelectTo()}
                                                        {(this.state.error == 'selectedSymbolOptionTo') && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Your Symbol </p>}

                                                    </td>
                                                </tr>

                                            </tbody>
                                        </table>
                                    </div>
                                </Col>
                            </Row>
                            <div className="save_button_filter">
                                <Button className="btn-primary" variant="primary" size="lg" block onClick={(e) => this.formDataSubmit(e)}>
                                    Save
                            </Button>
                            </div>
                        </form>
                    </div>
                </Grid>
            </div>
        );
    }
}

export default Innerfilter;
